<script>
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  data () {
    return {
      userInfo: null,
      taskData: [],
      userIsCompleted: true
    }
  },
  methods: {
    loadData () {
      let me = this
      this.$http.post('/user/queryuserinfo', {}, res => {
        if (res.success) {
          me.userInfo = res.data
        }
      })
      me.$http.post('/homepage/queryplatformtasklist', {}, res => {
        console.log(res)
        if (res.success) {
          me.taskData = res.data
          for (var i = 0; i < me.taskData.length; i++) {
            if (me.taskData[i].taskCode === '1001') {
              me.userIsCompleted = false
            }
          }
        }
      })
    },
    goUrl (obj) {
      if (this.userIsCompleted === false) {
        this.$alert('请先完善个人信息！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      } else {
        window.location = obj.taskUrl
      }
    },
    setting () {
      window.location = 'personal-set.html'
    },
    checkGo (url) {
      if (this.userIsCompleted === false) {
        this.$alert('请先完善个人信息！', '提示', {
          confirmButtonText: '确定'
        })
        return false
      } else {
        window.location = url
      }
    }
  }
}
</script>

